import React from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';

import AnimalIcon from '../images/icons/animal-fat.svg'
import FishIcon from '../images/icons/fish-fat.svg'
import BioIcon from '../images/icons/biodiesel.svg'
import AcidIcon from '../images/icons/acids.svg'
import VegetableIcon from '../images/icons/vegetable.svg'
import ByprodIcon from '../images/icons/by-prod.svg'
import ReactSVG from "react-svg";

const prods = [
    {to: '#biodiesel', title: 'Biodiesel', img: BioIcon},
    {to: '#animalfat', title: 'Animal Fat', img: AnimalIcon},
    {to: '#fishfat', title: 'Fish Fat', img: FishIcon},
    {to: '#vegoil', title: 'Vegetable Oil', img: VegetableIcon},
    {to: '#byprod', title: 'By-products of oil', img: ByprodIcon},
    {to: '#acids', title: 'Fatty acids', img: AcidIcon},
];

function Products() {
    return (
        <div className='products'>
            <div className="row container mx-auto text-center">
                    {prods.map((prod) => (
                        <div className='col-6 col-md-4 col-lg-2'>
                            <AnchorLink href={prod.to}><ReactSVG className='products-icon' src={prod.img} descr='icon'/></AnchorLink>
                            <h4 className='products-text'>  {prod.title}</h4>
                        </div>
                    ))}
            </div>
        </div>
    );
}

export default Products;
