import React from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';

const links = [
    {link: 'About', href: '#about'},
    {link: 'Benefits', href: '#benefits'},
    {link: 'Products', href: '#products'},
    {link: 'Contacts', href: '#contacts'},
];

class Navbar extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            show: true,
            scrollPos: 0
        };
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        this.setState({
            scrollPos: document.body.getBoundingClientRect().top,
            show: document.body.getBoundingClientRect().top > this.state.scrollPos
        });
    };

    render() {
        return (
            <div className={this.state.show ? "navbarr-active navbarr" : "navbarr-hidden navbarr"} onScroll={this.handleScroll}>
                <ul className='navbarr-list'>
                    {links.map(({link, href}) => (
                        <li key={link} className='navbarr-item'><AnchorLink href={href}>{link}</AnchorLink></li>
                    ))}
                </ul>
            </div>
        );
    }
}

export default Navbar;
